.search {
    padding: 0px 40px 0px 70px;
    width: 98vw;
    height: 480px;
    gap: 10px;
    flex-shrink: 0;
}

.searchBar {
    display: flex;
    height: 480px;
    padding: 96px 0px;
    justify-content: center;
    align-items: center;
    width: 100%;
    gap: 10px;
    flex-shrink: 0;
    border-radius: 32px;
    border: 2px solid var(--main-color-primary);
    background: var(--main-color-Tertiary);
}


.search_three_one {
    width: 808px;
    height: 60px;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 10px;
    margin-bottom: 30px;
}

.search_three_one input {
    margin-top: 64px;
    width: 808px;
    height: 60px;
    gap: 10px;
    border-radius: 16px;
}

.search_btn {
    display: flex;
    height: 40px;
    margin-top: 2rem;
    width: 134px;
    justify-content: center;
    align-items: center;
    gap: 8px;
    border-radius: 8px;
    background: var(--main-color-second);
    color: #fff;
    display: flex;
    padding: 10px 24px;
    flex: 1 0 0;
    align-self: stretch;
    position: absolute;
    right: 1.5rem;
}

.search_two h1 {
    color: var(--gray-one);
    text-align: center;
    font-family: 'Noto Serif', serif;
    font-size: 36px;
    font-style: normal;
    font-weight: 700;
    line-height: 48px;
    letter-spacing: 0.1px;
}

.search_two h2 {
    color: var(--gray-two);
    text-align: center;
    font-family: 'Open Sans', sans-serif;
    font-size: 18px;
    font-style: normal;
    font-weight: 400;
    line-height: 28px;
}

.search_three_two h4 {
    background: #fff;
    display: flex;
    border-radius: 32px;
    padding: 8px 32px;
    align-items: center;
    margin-top: 44px;
    gap: 10px;
}

@media screen and (max-width: 1110px) {
    .search {
        margin-top: 20%;
    }
}

@media screen and (max-width: 920px) {
    .search {
        margin-top: 33%;
        margin-bottom: -23%;
    }

    .search_three_one {
        width: 600px;
        height: 40px;
        padding: 0px;
        margin-bottom: 30px;
    }

    .searchBar {
        height: 300px;
    }
}

@media screen and (max-width: 750px) {
    .search_three_one {
        width: 500px;
        height: 30px;
        padding: 0px;
        margin-bottom: 30px;
    }

    .search_btn {
        margin: 1.65rem;
        right: -0.5rem;
    }

    .search {
        padding: 0px 20px 0px 30px;
    }
}