.news_article {
    padding: 80px;
    width: 100%;
    height: 100%;
}

.news_imgs {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: auto;
    gap: 36px;
}

.news_imgs .news_img_one {
    width: 380px;
    height: 380px;
}

.news_imgs .news_img_one img {
    width: 380px;
    height: 380px;
    border-radius: 16px;
    background: #FFF;
}

.news_img_one p {
    position: absolute;
    top: 70%;
    left: 17px;
    width: 290px;
    color: #FFF;
    font-family: Open Sans;
    font-size: 20px;
    font-style: normal;
    font-weight: 600;
    line-height: 28px;
}

.news_btn {
    width: 100%;
    height: auto;
    display: flex;
    justify-content: center;
    align-items: center;
}

.news_btn button {
    height: 40px;
    gap: 8px;
    flex-shrink: 0;
    border-radius: 8px;
    width: 170px;
    background: var(--main-color-primary);
}

.news {
    display: flex;
    align-items: center;
    justify-content: center;
}


@media screen and (max-width: 1250px) {

    .news_imgs .news_img_one {
        width: 340px;
        height: 340px;
    }


    .news_btn {
        margin-top: 50px;
    }
}

@media screen and (max-width: 1110px) {
    .news_imgs .news_img_one {
        width: 320px;
        height: 250px;
    }

    .news_imgs .news_img_one img {
        width: 320px;
        height: 260px;
        border-radius: 16px;
        background: #FFF;
    }

    .news_imgs {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-template-rows: auto;
        gap: 60px;
    }


}

@media screen and (max-width: 840px) {
    .news_imgs .news_img_one {
        width: 100%;
        height: 100%;

    }

    .news_imgs .news_img_one {
        width: 320px;
        height: 250px;
    }

    .news_imgs .news_img_one img {
        width: 320px;
        height: 280px;
        border-radius: 16px;
        background: #FFF;
    }

    .news_imgs {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-template-rows: auto;
        gap: 40px;
    }

    .news_article {
        padding: 60px;
    }

    .news_btn {
        top: 100%;
        /* left: 25%; */
    }
}

@media screen and (max-width: 550px) {
    .news_imgs .news_img_one {
        width: 100%;
        height: 100%;

    }

    .news_imgs {
        display: grid;
        grid-template-columns: repeat(1, 1fr);
        grid-template-rows: auto;
        gap: 40px;
    }

    .news_imgs .news_img_one {
        width: 320px;
        height: 250px;
    }

    .news_imgs .news_img_one img {
        width: 320px;
        height: 280px;
        border-radius: 16px;
        background: #FFF;
    }
}

@media screen and (max-width: 740px) {

    .news_imgs .news_img_one {
        width: 100%;
        height: 100%;
    }

    .news_article {
        padding: 40px;
    }

    .news_img_one p {
        position: absolute;
        top: 70%;
        left: 17px;
        width: 220px;
    }
}