.input {
    width: 44px;
    height: 44px;
    text-align: center;
    font-family: 'Lato', sans-serif;
    font-size: 32px;
    font-style: normal;
    font-weight: 400;
    line-height: 32px;
    letter-spacing: 0.1px;
    color: var(--gray-five);
    gap: 10px;
    font-size: 16px;
    cursor: pointer;
    pointer-events: none;
    text-align: center;
    background: var(--gray-one);
    border-radius: 8px;
}

.input:focus {
    outline: none;
    border-color: none;
}

.input:nth-child(1) {
    cursor: pointer;
    pointer-events: all;
    outline-color: none;
}

.input:nth-child(2) {
    cursor: pointer;
    pointer-events: all;
}

.input:nth-child(3) {
    cursor: pointer;
